import React from 'react'
import Color from 'color'
import { BoxContainer } from '../../elements/box'
import { FlexContainer, FullContainer } from '../../elements/flex'
import { SkillCheck } from '../SkillChecks'
import { StatsPreview } from '../StatsPreview'
import { Button } from '../../elements/button'
import { tWeapon } from '../../game/Weapon/type'
import { ITEM_RARITY_COLORS } from '../../game/Item/constants'
import { Icon } from '../Icon'
import { WEAPON_TYPE_ICONS } from '../../icons/maps'
import Hands from '../../icons/svg/lorc/hand.svg'
import Ranged from '../../icons/svg/delapouite/arrow-wings.svg'
import Melee from '../../icons/svg/lorc/battered-axe.svg'
import { FakeLink } from '../../elements/typography'
import { HoverToolTip } from '../Tooltip'
import { SkillPreview } from '../SkillPreview'
import { tProcessedCharacter } from '../../game/Character/type'

export const getDamageColor = (weapon: tWeapon) => {
  if (weapon.damage.type === 'physical') return 'lightblue'
  if (weapon.damage.type === 'magic') return 'plum'
  return 'white'
}

export interface WeaponPreviewPropsT {
  character?: tProcessedCharacter
  weapon: tWeapon
  showEquipButton?: boolean
  showBuyButton?: boolean
  cost?: number
  onEquipClick?: () => void
  onBuyClick?: () => void
}
export const WeaponPreview = (props: WeaponPreviewPropsT) => {
  const {
    character,
    weapon,
    showEquipButton,
    showBuyButton,
    cost = 0,
    onEquipClick,
    onBuyClick,
  } = props
  const basicAttack = weapon.skills[0]
  const rarityColor = ITEM_RARITY_COLORS[weapon.rarity]
  const from = Color(rarityColor).darken(0.5).rgb().toString()
  const to = Color(rarityColor).darken(0.7).rgb().toString()
  const gradient = `linear-gradient(180deg, ${from} 0%, ${to} 100%)`
  return (
    <BoxContainer style={{ width: 240 }} substyle={{ background: gradient }}>
      <FlexContainer $direction='column'>
        {basicAttack && (
          <FlexContainer style={{ justifyContent: 'center', marginBottom: 10 }}>
            <FlexContainer $full style={{ alignItems: 'center' }}>
              <Icon
                src={weapon.damage.range === 'melee' ? Melee : Ranged}
                size={16}
              />
            </FlexContainer>
            {Array(basicAttack.rolls)
              .fill(null)
              .map((_, i) => (
                <SkillCheck
                  key={i}
                  check={{ label: weapon.stat, result: true }}
                  size={18}
                  padding={4}
                  skill={weapon.skills[0]}
                />
              ))}
            <FlexContainer
              $full
              style={{
                fontSize: 18,
                justifyContent: 'flex-end',
                fontWeight: 'bolder',
                alignItems: 'center',
              }}
            >
              <Icon
                size={16}
                src={Hands}
                fill='rgba(255,255,255,0.8)'
                style={{ marginRight: 4 }}
              />
              {weapon.twoHand && '2'}
              {!weapon.twoHand && '1'}
            </FlexContainer>
          </FlexContainer>
        )}
        <FlexContainer style={{ alignItems: 'center' }}>
          <Icon
            src={WEAPON_TYPE_ICONS[weapon.type]}
            size={32}
            style={{ marginRight: 10 }}
            shadow
          />
          <FlexContainer
            $direction='column'
            style={{
              margin: '8px 0',
              textShadow: '1px 1px 2px rgba(0,0,0,0.8)',
              textAlign: 'left',
            }}
          >
            <span style={{ fontWeight: 600 }}>{weapon.name}</span>
            <span style={{ color: 'rgba(255,255,255,0.6)' }}>
              {weapon.rarity} {weapon.type}
            </span>
          </FlexContainer>
          <FullContainer />

          {showEquipButton && (
            <div style={{ marginLeft: 10 }}>
              <Button onClick={onEquipClick} style={{ padding: 4 }}>
                Equip
              </Button>
            </div>
          )}
        </FlexContainer>
        <BoxContainer
          substyle={{
            background: 'rgba(0,0,0,0.7)',
            minHeight: 120,
            textAlign: 'left',
          }}
        >
          <FlexContainer $direction='column'>
            <FlexContainer style={{ lineHeight: '24px', marginBottom: 4 }}>
              <span
                style={{
                  color: getDamageColor(weapon),
                  fontWeight: 'bolder',
                  marginRight: 8,
                  fontSize: 24,
                }}
              >
                {weapon.damage.value}
              </span>
              <span style={{ textTransform: 'capitalize' }}>
                {weapon.damage.type} Damage
              </span>
            </FlexContainer>
            <span
              style={{
                color: 'plum',
                marginBottom: 8,
                display: 'flex',
                flexWrap: 'wrap',
              }}
            >
              {weapon.skills.map((skill, i) => (
                <React.Fragment key={skill.id}>
                  {i > 0 ? ', ' : ''}
                  <HoverToolTip
                    direction='down'
                    content={
                      character ? (
                        <SkillPreview skill={skill} source={character} />
                      ) : (
                        <div />
                      )
                    }
                  >
                    <FakeLink style={{ paddingLeft: i === 0 ? 0 : 4 }}>
                      {skill.name}
                    </FakeLink>
                  </HoverToolTip>
                </React.Fragment>
              ))}
            </span>
            {weapon.breakable && (
              <div style={{ color: 'gold', fontSize: 12 }}>Breakable</div>
            )}
            <StatsPreview stats={weapon.stats} />
            {weapon.immunities.map((status) => (
              <span style={{ textTransform: 'capitalize', fontSize: 14 }}>
                {status} immunity
              </span>
            ))}
          </FlexContainer>
        </BoxContainer>
        {showBuyButton && (
          <FlexContainer style={{ justifyContent: 'center', marginTop: 10 }}>
            <Button onClick={onBuyClick} style={{ padding: '4px 16px' }}>
              Buy ({cost || 0})
            </Button>
          </FlexContainer>
        )}
      </FlexContainer>
    </BoxContainer>
  )
}
